@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@400;500;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #ebebeb;
  padding: 10px 20px;
}

.app {
  max-width: 600px;
  min-height: 600px;
  margin: 5px auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  padding: 30px 10px 60px;
  box-shadow: -1px 6px 28px 0px rgba(0, 0, 0, 0.75);
  background: #fff;
}

.header a {
  text-decoration: none;
}

.header__title {
  font-family: "Montserrat Alternates", sans-serif;
  margin: 0 0 50px;
  text-decoration: none;
  color: #4285f4;
}

.logout {
  position: relative;
  top: -120px;
  /* top: 25px; */
  /* left: 950px; */
  float: right;
  /* position: absolute; */
}

.logout-btn {
  display: inline-block;
  padding: 5px;
  margin-right: 10px;
  background-color: #fff;
  border: 1px solid #4285f4;
  border-radius: 5px;
}

.logout-btn:hover {
  color: #4285f4;
  cursor: pointer;
}

.create-train-page {
  padding: 0 30px 50px;
  width: 100%;
  box-sizing: border-box;
}

.create-train-page__title {
  margin: 0 0 20px;
  text-align: center;
  font-size: 20px;
}

.create-train-page__properties {
  margin-bottom: 40px;
}

.create-train-property {
  font-size: 14px;
  text-align: left;
  max-width: 400px;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding: 0;
  margin-bottom: 20px;
}

.create-train-property__input {
  border: 1px solid #ababab;
  padding: 5px 10px;
  border-radius: 5px;
}

.create-train-workout-list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  padding: 0;
  list-style-type: none;
  margin: 0 auto 40px;
}

.create-train-workout-list__button {
  height: 50px;
  background-color: #fff;
  border: 1px solid #4285f4;
  border-radius: 5px;
}

.create-train-workout-list__button:hover {
  color: #4285f4;
  cursor: pointer;
}

.create-train-program {
  margin-bottom: 40px;
}

.create-train-program-item {
  display: flex;
  align-items: baseline;
  padding-bottom: 5px;
}

.create-train-program-item__title {
  width: 200px;
  margin: 0 20px 0 0;
  display: flex;
  justify-content: space-between;
}

.create-train-program-item__property {
  display: flex;
  align-items: baseline;
  margin-right: 30px;
}

.create-train-program-item__input {
  border: 1px solid #ababab;
  padding: 5px;
  border-radius: 5px;
  width: 40px;
  box-sizing: border-box;
  margin-right: 5px;
}

.create-train-program-item__remove {
  text-decoration: underline;
  color: #f22;
  background-color: #fff;
  border: none;
  font-size: 14px;
}

.create-train-program-item__remove:hover {
  cursor: pointer;
  opacity: 0.7;
}

.create-train-program__save {
  display: block;
  margin: 0 auto;
  width: 100px;
  height: 50px;
  background-color: #fff;
  border: 1px solid #4285f4;
  border-radius: 5px;
}

.create-train-program__save:hover {
  color: #4285f4;
  cursor: pointer;
}

a {
  display: block;
  text-align: center;
}

.create-train-program-item__remove-altr {
  display: none;
}

.create-train-program-item__description-button {
  padding: 0;
  background-color: #fff;
  border: 1px solid #4285f4;
  outline: none;
  display: inline-block;
  margin-right: 10px;
  padding: 5px 10px;
  border-radius: 5px;
}

.create-train-program-item__description-button:hover {
  color: #4285f4;
  cursor: pointer;
}

.create-train-program-item__description {
  border: 1px solid #4285f4;
  border-radius: 10px;
  z-index: 1050;
  display: flex;
  justify-content: space-between;
  background-color: white;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -200%);
  width: 45%;
}

.create-train-program-item__description-info {
  width: 95%;
  margin: 0;
}

.create-train-program-item__description-info-title {
  margin: 5px;
}
.create-train-program-item__description-info-text {
  margin: 0 5px;
  font-size: 12px;
}
.create-train-program-item__description-close {
  width: 20px;
  height: 20px;
  color: #f22;
  padding: 0;
  background-color: #fff;
  border-radius: 100px;
  margin: 5px;
}

@media screen and (min-width: 320px) and (max-width: 991px) {
  .create-train-workout-list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    padding: 0;
    list-style-type: none;
    margin: 0 auto 40px;
  }
  .create-train-program-item__title {
    font-size: 12px;
    width: 70%;
    margin-right: 5px;
  }

  .create-train-program-item__property {
    display: flex;
    align-items: baseline;
    font-size: 12px;
    margin-right: 5px;
    padding: 0;
    width: 20%;
  }

  .create-train-program-item__input {
    width: 40px;
  }
  .create-train-program-item__remove-altr {
    display: inline-block;
    background-color: #fff;
    border: 1px solid red;
    color: red;
    border-radius: 5px;
    padding: 5px 10px;
  }

  .create-train-program-item__remove {
    display: none;
  }
}

.main-page {
  padding: 0 30px 50px;
  width: 100%;
  box-sizing: border-box;
}

.main-page__choose-action {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
  width: 100%;
}

.main-page__choose-action-btn {
  display: block;
  margin: 0 auto;
  height: 50px;
  background-color: #fff;
  border: 1px solid #4285f4;
  border-radius: 5px;
  padding: 0 20px;
}

.main-page__choose-action-btn:hover {
  color: #4285f4;
  cursor: pointer;
}
.main-page a {
  text-decoration: none;
  color: black;
}

.main-page__title {
  margin: 0 0 20px;
  text-align: center;
  font-size: 20px;
}
.main-page__trains {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0.5rem;
}

.choose-train-cart {
  width: 90%;
  height: 86px;
  text-align: left;
  padding: 5px;
  background-color: #fff;
  border: 1px solid #4285f4;
  border-radius: 5px;
}

.choose-train-cart:hover {
  color: #4285f4;
  cursor: pointer;
}

.choose-train-cart__title {
  text-align: center;
  margin: 10px;
}

.choose-train-cart__date {
  text-align: center;
  margin: 0;
  font-size: 12px;
}

@media screen and (min-width: 320px) and (max-width: 991px) {
  .main-page__trains {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 0.5rem;
  }
  .main-page__choose-action-btn {
    margin: 0 5px;
  }
  .choose-train-cart__title {
    font-size: 14px;
  }
}

.history-page {
  padding: 0 30px 50px;
  width: 100%;
  box-sizing: border-box;
}
.history-page__title {
  margin: 0 0 20px;
  text-align: center;
  font-size: 20px;
}
.history-page__trains {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0.5rem;
}

.history-page a {
  text-decoration: none;
  color: black;
}

@media screen and (min-width: 320px) and (max-width: 991px) {
  .history-page__trains {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 0.5rem;
  }
  .history-page__title {
    font-size: 14px;
  }
}

.train-page {
  padding: 0 30px 50px;
  width: 100%;
  box-sizing: border-box;
}
.train-page__title {
  margin: 0 0 20px;
  text-align: center;
  font-size: 20px;
}
textarea {
  display: block;
}
.train-page-item {
  display: flex;
  align-items: baseline;
}
.train-page-item__title {
  width: 250px;
  margin-right: 10px;
}
.train-page-item__property {
  display: flex;
  align-items: baseline;
  margin-right: 30px;
}

.train-page-item__input {
  border: 1px solid #ababab;
  padding: 5px;
  border-radius: 5px;
  width: 40px;
  box-sizing: border-box;
  margin-right: 5px;
}

.train-page_text-area {
  display: block;
  margin: 0 auto;
  width: 50%;
  height: 50px;
  border: 1px solid #ababab;
  padding: 5px 10px;
  border-radius: 5px;
  box-sizing: border-box;
}

.train-page_complete-button {
  display: block;
  margin: 20px auto;
  height: 50px;
  background-color: #fff;
  border: 1px solid #4285f4;
  border-radius: 5px;
}

.train-page_complete-button:hover {
  color: #4285f4;
  cursor: pointer;
}

a {
  margin-top: 20px;
  display: block;
  text-align: center;
}

@media screen and (min-width: 320px) and (max-width: 991px) {
  .train-page-item__title {
    width: 60%;
    font-size: 12px;
    margin-right: 5px;
  }
  .train-page-item__property {
    display: flex;
    align-items: baseline;
    font-size: 12px;
    margin-right: 5px;
    padding: 0;
    width: 40%;
  }
  .train-page-item__input {
    border: 1px solid #ababab;
    padding: 5px;
    border-radius: 5px;
    width: 35px;
    box-sizing: border-box;
    font-size: 12px;
  }
  .train-page-item__counter {
    font-size: 12px;
  }
}

.header-past-train {
  display: flex;
  justify-content: space-between;
}

.past-train-page__button-back {
  display: block;
  margin: 20px auto;
  height: 50px;
  background-color: #fff;
  border: 1px solid #4285f4;
  border-radius: 5px;
}

.past-train-page__button-back:hover {
  color: #4285f4;
  cursor: pointer;
}

.past-train-page__link-back {
  text-decoration: none;
}

.past-train-page {
  padding: 0 30px 50px;
  width: 100%;
  box-sizing: border-box;
}
.past-train-page__title {
  margin: 0 0 20px;
  text-align: center;
  font-size: 20px;
}
.past-train-page__item {
  display: flex;
  align-items: baseline;
}
.past-train-page__item-title {
  width: 300px;
}
.past-train-page__item-property {
  display: flex;
  align-items: baseline;
  margin-right: 30px;
}
.past-train-page__comment {
  margin: 0 auto;
  padding: 10px;
  width: 50%;
  height: 50px;
  font-size: 12px;
}

@media screen and (min-width: 320px) and (max-width: 991px) {
  .past-train-page__item-title {
    width: 60%;
    font-size: 12px;
    margin-right: 5px;
  }
  .past-train-page__item-property {
    display: flex;
    align-items: baseline;
    font-size: 12px;
    margin-right: 10px;
    width: 40%;
  }
}

.auth {
  width: 250px;
  padding: 10px;
  height: 350px;
  /* position: absolute; */
  /* border: 1px solid black;
  background-color: white; */
  /* left: 50%;
  transform: translateX(-50%); */
}

.auth-close {
  position: absolute;
  left: 252px;
  top: 5px;
  background-color: white;
  outline: none;
  border: 1px solid red;
  border-radius: 20px;
  color: black;
  width: 15px;
  height: 15px;
  padding: 0;
}

.auth-close:hover {
  color: red;
}

.auth-form-item {
  width: 100%;
  padding-bottom: 5px;
}

.auth-form-item__title {
  font-size: 14px;
  width: 100%;
  display: block;
}

.auth-form-item__input {
  width: 95%;
}

.auth-choose-action {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.auth-choose-action__button {
  width: 100px;
  display: block;
  text-align: center;
}

.active {
  color: #4285f4;
  border: 1px solid #4285f4;
  border-radius: 2px;
}

.default {
  color: black;
}
.auth-form-btn {
  width: 100%;
  display: block;
  margin: 20px auto;
  height: 50px;
  background-color: #fff;
  border: 1px solid #4285f4;
  border-radius: 5px;
}

.auth-form-btn:hover {
  color: #4285f4;
  cursor: pointer;
}

